@import '../../assets/styles/core/base';
@import '../../assets/styles/core/setting';
@import '../../assets/styles/core/reset';
@import '../../assets/styles/core/common';
.servicelist_view {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  &.m_t {
    top: pr2(44);
    .list_cont {
      padding-bottom: pr2(200);
    }
    .mileage_toast {
      top: pr2(124);
    }
  }
  overflow: hidden;
}

// 更改全局样式
:global {
  .common_nodata_f {
    position: absolute;
    z-index: 10;
    margin-top: pr2(160) !important;
    padding-top: 0 !important;
  }
}
.car_item {
  width: 100%;
  padding: 0 pr2(20);
  box-sizing: border-box;
  height: pr2(60);
  background: url('data:image/png;base64,') no-repeat;
  background-size: cover;
  display: flex;
  justify-content: space-between;
  .car {
    flex: 1;
    display: flex;
    justify-content: center;
    font-size: pr2(16);
    line-height: 1;
    font-family: PingFangSC-Medium;
    color: #fff;
    overflow: hidden;
    margin-right: pr2(8);
    box-sizing: border-box;
    transform: translateY(6px);
    p {
      @include ellipsis();
      width: 80%;
    }
  }
  .icon_arrow_r {
    display: inline-block;
    width: pr2(12);
    height: pr2(12);
    background: url('data:image/png;base64,') no-repeat;
    background-size: cover;
    margin-left: pr2(4);
    &.icon_arrow_down {
      transform: rotate(90deg);
    }
    &.icon_arrow_up {
      transform: rotate(-90deg);
    }
  }
  .line {
    transform: translateY(6px);
    margin: 0 pr2(4);
    width: pr2(1);
    height: pr2(17);
    background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAAzCAYAAABSUrISAAAAAXNSR0IArs4c6QAAAINJREFUKBWdkF0OgDAIg5l6BM/l/Q8zgxRDI2HGnweGlfKtKqq6Wm3ok4g0K/TzsL5YtevEBR0xubfN4QUagjQKOChSgrRTbAntSSIOadjBSz8SLYlw0Pbne5gAtALgH9W4DA+7lWKxiB4T625zAGyclJ1EG9s+0MaA5wT80pdxyj1yACu/RNGTwjcMAAAAAElFTkSuQmCC') no-repeat;
    background-size: cover;
  }
  .mileage {
    transform: translateY(6px);
    flex: 1;
    font-size: pr2(16);
    line-height: 1;
    font-family: PingFangSC-Medium;
    color: #fff;
    text-align: center;
  }
}
.list_cont {
  position: relative;
  z-index: 10;
  width: 100%;
  height: 100%;
  overflow: hidden;
  overflow-y: auto;
  box-sizing: border-box;
  transform: translateY(-24px);
  padding: 0 pr2(20);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  padding-top: pr2(22);
  padding-bottom: pr2(160);
  background: #fff;
  -webkit-overflow-scrolling: touch;
  .list {
    li {
      padding: pr2(16) pr2(12);
      @extend %shadow;
      border-radius: 6px;
      margin-bottom: pr2(12);
    }
  }
  .cell_hd {
    display: flex;
    align-items: center;
  }
  .cell_bd {
    padding-top: pr2(6);
    padding-left: pr2(34);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .icon_circle {
    display: inline-block;
    width: pr2(22);
    height: pr2(22);
    background: url('data:image/png;base64,') no-repeat;
    background-size: 100% 100%;
  }
  .icon_circle_active {
    display: inline-block;
    width: pr2(22);
    height: pr2(22);
    background: url('data:image/png;base64,') no-repeat;
    background-size: 100% 100%;
  }
  .name {
    margin-left: pr2(12);
    color: #111E36;
    font-size: pr2(18);
    font-family: PingFangSC-Medium;
  }
  .tips_btn {
    position: relative;
    width: 72px;
    height: 22px;
    margin-left: pr2(12);
    line-height: 22px;
    font-size: 12px;
    color: #0055FF;
    font-family: PingFangSC-Light;
    text-align: center;
    &:before {
      content: " ";
      width: 200%;
      height: 200%;
      position: absolute;
      top: 0;
      left: 0;
      border: 1px solid rgba(0, 85, 255, 0.5);
      transform: scale(0.5);
      transform-origin: 0 0;
      box-sizing: border-box;
      border-radius: 100px;
    }
    .icon_arrow_blue {
      display: inline-block;
      width: pr2(8);
      height: pr2(8);
      background: url('data:image/png;base64,') no-repeat;
      background-size: 100% 100%;
    }
  }
  .txt {
    font-size: pr2(12);
    color: #666D7F;
    font-family: PingFangSC-Regular;
  }
  .price {
    font-size: pr2(16);
    color: #0055FF;
    font-family: YCZJ-DINAlternate;
    i {
      margin-left: pr2(4);
      font-size: pr2(10);
      color: #666D7F;
      font-family:PingFangSC-Regular;
    }
  }
}


.btn_item {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 10001;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 66px;
  background: #fff;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom);
  &.unable {
    .btn_cell {
      background: #F1F3F6;
      color: #C5CAD4;
    }
  }
  .maintain_item {
    flex: 0 0 pr2(100);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #111E36;
    font-size: pr2(10);
    font-family:PingFangSC-Regular
  }
  .icon_maintain {
    width: pr2(22);
    height: pr2(22);
    background: url('data:image/png;base64,') no-repeat;
    background-size: cover;
  }
  .btn_cell {
    flex: 1;
    margin-right: pr2(20);
    display: block;
    background: #0055FF;
    border-radius: 4px;
    color: #fff;
    font-size: pr2(16);
    font-family: PingFangSC-Regular;
    text-align: center;
    line-height: pr2(50);
  }
}

.mileage_toast {
  position: absolute;
  top: pr2(36);
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10003;
  background: rgba(0, 0, 0, 0.5);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  .mileage_cont {
    max-height: pr2(230);
    overflow: hidden;
    overflow-y: auto;
    text-align: center;
    background: #fff;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    -webkit-overflow-scrolling: touch;
    li {
      height: pr2(50);
      line-height: pr2(50);
      color: #111E36;
      font-size: pr2(16);
      font-family: PingFangSC-Regular;
    }
  }
}