@import '../../assets/styles/core/base';
@import '../../assets/styles/core/setting';
@import '../../assets/styles/core/reset';

.toast_mask {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 10003;
  background: rgba(0, 0, 0, 0.5);
}
.toast_item {
  position: fixed;
  top: 30%;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10005;
  height: 70%;
  .toast_cont {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    background: #fff;
  }
  .name {
    padding: pr2(17);
    text-align: center;
    color: #111E36;
    font-family:PingFangSC-Medium;
    font-weight: 500;
  }
  .img_cont {
    position: absolute;
    height: 97%;
    overflow: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    img {
      display: block;
      width: 100%;
    }
  }
  .close_btn {
    position: absolute;
    right: pr2(20);
    top: pr2(20);
    width: pr2(22);
    height: pr2(22);
    background: url('data:image/png;base64,') no-repeat;
    background-size: cover;
  }
}

:global {
  .toast-enter {
    transform: translate3d(0, 100%, 0); // 初始位置
  }
  .toast-enter.toast-enter-active {
    transition: all 400ms ease;
    transform: translate3d(0, 0, 0); // 目标位置
  }
  
  .toast-leave {
    transform: translate3d(0, 0, 0); // 目标位置
  }
  .toast-leave.toast-leave-active {
    transition: all 400ms ease;
    transform: translate3d(0, 100%, 0); // 初始位置
  }


  .mask-enter {
    opacity: 0; // 初始位置
  }
  .mask-enter.mask-enter-active {
    transition: all 400ms ease;
    opacity: 1; // 目标位置
  }
  .mask-leave {
    opacity: 1; // 目标位置
  }
  .mask-leave.toast-leave-active {
    opacity: 0; // 初始位置
    transition: all 400ms ease;
  }
}