 @import '../../assets/styles/core/base';
 @import '../../assets/styles/core/setting';
 @import '../../assets/styles/core/reset';


 .error_tips {
  position: relative;
  display: block;
  height: 100%;
  width: 100%;
  min-height: pr(500);
  text-align: center;
  color: #999;
  padding-top: pr2(200);
  padding-bottom: pr2(60);
  p {
    margin-top: pr2(24);
    color: #666d7f;
    font-size: pr2(13);
  }
  .reload_btn {
    margin: 0 auto;
    margin-top: pr2(24);
    width: pr2(108);
    height: pr2(40);
    line-height: pr2(40);
    border-radius: pr2(4);
    text-align: center;
    color: rgba(0, 85, 255, 1);
    font-size: pr2(14);
    font-family: PingFangSC-Regular;
    font-weight: 400;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      width: 200%;
      height: 200%;
      top: 0;
      left: 0;
      border: 1px solid rgba(0,85,255,0.2);;
      transform: scale(0.5);
      transform-origin: 0 0;
      box-sizing: border-box;
      border-radius: 4px;
    }
  }
}
.nocontent_icon {
  width: pr2(160);
  height: pr2(160);
  margin: 0 auto;
  background: url('data:image/png;base64,') no-repeat;
  background-size: 100% 100%;
}